.Center_{
    margin-top: 50px;
    display: flex;
    justify-content: center;
    align-items: center;
}
.Holder_{
    border-top: 1px solid #1d1d1d;
    width: 100%;
    height: 100%;
}
.Content{
    width: 100%;
    height: 100%;
    overflow: scroll !important;
    scroll-snap-type: mandatory;
    overflow-x: hidden;
}
.AnimeCard{
    background-color: var(--Secoundry);
    border: none;
    height: 50px;
    width: 100%;
    display: grid;
    grid-template-rows:auto auto auto auto auto auto auto auto;
    grid-template-columns: auto auto auto auto auto auto auto auto;
    transition: all 100ms linear;

}
.AnimeCard:active{
    outline:#1d1d1d solid 1px ;
}
.AnimeIMG{
    float: left;
    width: 40px;
    height: 45px;
    padding: 0;

}
.Title{
    float: left;
    font-size: 10px;
    overflow: hidden;
    word-wrap: break-word;
    color: white;

}
.View_Full{
    border: none;
    border-top: 1px solid #1d1d1d !important;
    background-color: var(--Secoundry);
    width: 100%;
    height: 35px;
    color: #a4fcb5;
    font-size: 10px;
    display: flex;
    justify-content: center;
    align-items: center;
}
